import React, { useState, useEffect, useRef } from 'react';
import {
  Layout, Card, Button, Input, Upload, List, Tag, Space,
  Radio, message, Progress, Typography, Divider, Row, Col, Modal, Form, notification
} from 'antd';
import {
  InboxOutlined, FileTextOutlined, PlayCircleOutlined,
  DeleteOutlined, EyeOutlined, CloudUploadOutlined,
  CheckCircleOutlined, LoadingOutlined, FileAddOutlined, SettingOutlined,
  ApiOutlined, BellOutlined
} from '@ant-design/icons';
import './App.css';
import packageJson from '../package.json';

const { Header, Content, Sider } = Layout;
const { Title, Text, Paragraph } = Typography;
const { Dragger } = Upload;

function App() {
  const [files, setFiles] = useState([]);
  const [primaryFileId, setPrimaryFileId] = useState(null);
  const [uploading, setUploading] = useState(false);
  const [recognizingFileId, setRecognizingFileId] = useState(null);
  const [autoRecognizeId, setAutoRecognizeId] = useState(null);
  const [invoiceNumber, setInvoiceNumber] = useState('');
  const [clientOrderNumber, setClientOrderNumber] = useState('');
  const [totalAmount, setTotalAmount] = useState('');
  const [automating, setAutomating] = useState(false);
  const [logs, setLogs] = useState([]);
  const [processingProgress, setProcessingProgress] = useState(0);
  const [settingsVisible, setSettingsVisible] = useState(false);
  const [settingsForm] = Form.useForm();
  const [savingSettings, setSavingSettings] = useState(false);
  const [testingApi, setTestingApi] = useState(false);
  const [poMappings, setPoMappings] = useState([]);
  const logConsoleRef = useRef(null);
  const processingFilesRef = useRef(false);
  const titleFlashIntervalRef = useRef(null);

  // 
  useEffect(() => {
    if ('Notification' in window && Notification.permission === 'default') {
      Notification.requestPermission();
    }
  }, []);

  // 
  const showCompletionNotification = () => {
    // 1.  Ant Design 
    notification.success({
      message: ' Automation Complete!',
      description: 'The automation process has finished. Please check the results.',
      duration: 0, // 
      placement: 'topRight',
      icon: <BellOutlined style={{ color: '#52c41a' }} />,
      style: {
        width: 400,
        backgroundColor: '#f6ffed',
        border: '2px solid #52c41a',
        boxShadow: '0 4px 12px rgba(82, 196, 26, 0.3)',
      },
      onClick: () => {
        // 
        stopTitleFlash();
      }
    });

    // 2. 
    if ('Notification' in window && Notification.permission === 'granted') {
      const browserNotification = new Notification('Automation Complete!', {
        body: 'PLZ check - The automation process has finished successfully.',
        icon: '/favicon.ico',
        tag: 'automation-complete',
        requireInteraction: true, // 
      });

      browserNotification.onclick = () => {
        window.focus();
        browserNotification.close();
        stopTitleFlash();
      };
    }

    // 3. 
    startTitleFlash();

    // 4. 
    playNotificationSound();
  };

  // 
  const startTitleFlash = () => {
    const originalTitle = document.title;
    let isOriginal = true;

    titleFlashIntervalRef.current = setInterval(() => {
      document.title = isOriginal ? ' New Message!' : originalTitle;
      isOriginal = !isOriginal;
    }, 1000);
  };

  // 
  const stopTitleFlash = () => {
    if (titleFlashIntervalRef.current) {
      clearInterval(titleFlashIntervalRef.current);
      titleFlashIntervalRef.current = null;
      document.title = 'Invoice Automation'; // 
    }
  };

  // 
  const playNotificationSound = () => {
    try {
      // 
      const audioContext = new (window.AudioContext || window.webkitAudioContext)();
      const oscillator = audioContext.createOscillator();
      const gainNode = audioContext.createGain();

      oscillator.connect(gainNode);
      gainNode.connect(audioContext.destination);

      oscillator.frequency.value = 800; // 
      oscillator.type = 'sine';

      gainNode.gain.setValueAtTime(0.3, audioContext.currentTime);
      gainNode.gain.exponentialRampToValueAtTime(0.01, audioContext.currentTime + 0.5);

      oscillator.start(audioContext.currentTime);
      oscillator.stop(audioContext.currentTime + 0.5);
    } catch (error) {
      console.log(':', error);
    }
  };

  // 
  useEffect(() => {
    const handleVisibilityChange = () => {
      if (document.visibilityState === 'visible') {
        stopTitleFlash();
      }
    };

    const handleFocus = () => {
      stopTitleFlash();
    };

    document.addEventListener('visibilitychange', handleVisibilityChange);
    window.addEventListener('focus', handleFocus);

    return () => {
      document.removeEventListener('visibilitychange', handleVisibilityChange);
      window.removeEventListener('focus', handleFocus);
      stopTitleFlash();
    };
  }, []);

  // Removed global EventSource - now created per-session in handleAutomation

  // Auto scroll logs to bottom
  useEffect(() => {
    if (logConsoleRef.current) {
      logConsoleRef.current.scrollTop = logConsoleRef.current.scrollHeight;
    }
  }, [logs]);

  // Auto recognize when only one file is uploaded
  useEffect(() => {
    if (autoRecognizeId && files.length > 0) {
      const fileExists = files.find(f => f.id === autoRecognizeId);
      if (fileExists) {
        addLog({ level: 'INFO', message: '...', timestamp: new Date().toISOString() });
        handleRecognizeFile(autoRecognizeId);
        setAutoRecognizeId(null);
      }
    }
  }, [autoRecognizeId, files]);

  const addLog = (logData) => {
    setLogs(prevLogs => {
      const newLogs = [...prevLogs, logData];
      return newLogs.slice(-100);
    });
  };

  //  - 
  const handleFiles = async (selectedFiles) => {
    console.log(' handleFiles called with', selectedFiles?.length, 'files');

    //  beforeUpload 
    if (processingFilesRef.current) {
      console.log(' Already processing files, skipping...');
      return;
    }

    if (!selectedFiles || selectedFiles.length === 0) return;

    // 
    const validTypes = ['application/pdf', 'image/jpeg', 'image/png', 'image/jpg'];

    const validFiles = selectedFiles.filter(file => {
      if (!validTypes.includes(file.type)) {
        message.warning(`: ${file.name}`);
        return false;
      }
      if (file.size > 10 * 1024 * 1024) {
        message.warning(`: ${file.name} (10MB)`);
        return false;
      }
      return true;
    });

    if (validFiles.length === 0) {
      message.error('PDFJPGPNG');
      return;
    }

    // 
    processingFilesRef.current = true;
    setUploading(true);

    try {
      const fileTypes = validFiles.map(f => f.type.includes('pdf') ? 'PDF' : '').join('');
      addLog({ level: 'INFO', message: ` ${validFiles.length}  (${fileTypes})`, timestamp: new Date().toISOString() });

      const timestamp = Date.now();
      const newFiles = [];
      let primaryId = null;

      validFiles.forEach((file, index) => {
        const url = URL.createObjectURL(file);
        const tempId = `temp-${timestamp}-${index}`;
        console.log(` Creating file entry: ${file.name} -> ${tempId}`);

        const fileInfo = {
          id: tempId,
          name: file.name,
          size: file.size,
          url: url,
          fileId: null, // Not uploaded yet
          file: file, // Store the actual File object for later upload
          invoiceNumber: undefined,
          clientOrderNumber: undefined,
          totalAmount: undefined,
          uploaded: false // Mark as not uploaded
        };

        newFiles.push(fileInfo);

        if (index === 0) {
          primaryId = tempId;
        }

        addLog({ level: 'INFO', message: ` : ${file.name}`, timestamp: new Date().toISOString() });
      });

      console.log(` Adding ${newFiles.length} new files to state`);
      setFiles(prevFiles => {
        console.log(` Previous files: ${prevFiles.length}, New files: ${newFiles.length}`);
        return [...prevFiles, ...newFiles];
      });

      if (primaryId) {
        setPrimaryFileId(primaryId);
      }

      if (newFiles.length === 1 && primaryId) {
        setAutoRecognizeId(primaryId);
      }

      addLog({ level: 'INFO', message: `""AI`, timestamp: new Date().toISOString() });
    } catch (error) {
      message.error(': ' + error.message);
    } finally {
      setUploading(false);
      //  beforeUpload 
      setTimeout(() => {
        processingFilesRef.current = false;
        console.log(' Processing flag reset');
      }, 100);
    }
  };

  const handleSelectPrimaryFile = (fileId) => {
    setPrimaryFileId(fileId);
    const selectedFile = files.find(f => f.id === fileId);
    if (selectedFile) {
      addLog({ level: 'INFO', message: `: ${selectedFile.name}`, timestamp: new Date().toISOString() });
      if (selectedFile.invoiceNumber !== undefined) {
        setInvoiceNumber(selectedFile.invoiceNumber || '');
        setClientOrderNumber(selectedFile.clientOrderNumber || '');
        setTotalAmount(selectedFile.totalAmount || '');
      }
    }
  };

  const handleRecognizeFile = async (fileId) => {
    const fileToRecognize = files.find(f => f.id === fileId);
    if (!fileToRecognize) {
      message.error('');
      return;
    }

    setRecognizingFileId(fileId);
    addLog({ level: 'INFO', message: `: ${fileToRecognize.name}`, timestamp: new Date().toISOString() });

    try {
      // Use the stored File object or fetch from blob URL
      const file = fileToRecognize.file || await (async () => {
        const response = await fetch(fileToRecognize.url);
        const blob = await response.blob();
        return new File([blob], fileToRecognize.name, { type: 'application/pdf' });
      })();

      const formData = new FormData();
      formData.append('pdf', file);

      // Upload with AI processing
      const uploadResponse = await fetch('/api/upload-pdf?useAI=true', {
        method: 'POST',
        body: formData,
      });

      const result = await uploadResponse.json();

      if (result.success) {
        // Update the file with the backend-generated fileId and extracted data
        setFiles(prevFiles => prevFiles.map(f => {
          if (f.id === fileId) {
            return {
              ...f,
              fileId: result.fileInfo.id, // Set the real backend fileId
              uploaded: true, // Mark as uploaded
              invoiceNumber: result.data.invoiceNumber || '',
              clientOrderNumber: result.data.clientOrderNumber || '',
              totalAmount: result.data.totalAmountExGST || ''
            };
          }
          return f;
        }));

        message.success(`: ${fileToRecognize.name}`);
        addLog({ level: 'INFO', message: ` : ${fileToRecognize.name}`, timestamp: new Date().toISOString() });
        addLog({ level: 'INFO', message: `: ${result.data.invoiceNumber || 'N/A'}`, timestamp: new Date().toISOString() });
        addLog({ level: 'INFO', message: `: ${result.data.clientOrderNumber || 'N/A'}`, timestamp: new Date().toISOString() });
        addLog({ level: 'INFO', message: `: ${result.data.totalAmountExGST || 'N/A'}`, timestamp: new Date().toISOString() });

        if (fileId === primaryFileId) {
          setInvoiceNumber(result.data.invoiceNumber || '');
          setClientOrderNumber(result.data.clientOrderNumber || '');
          setTotalAmount(result.data.totalAmountExGST || '');
        }
      } else {
        message.error(`: ${result.error || ''}`);
      }
    } catch (error) {
      message.error(`: ${error.message}`);
    } finally {
      setRecognizingFileId(null);
    }
  };

  const handleRemoveFile = (fileId) => {
    const fileToRemove = files.find(f => f.id === fileId);
    if (fileToRemove) {
      URL.revokeObjectURL(fileToRemove.url);
      addLog({ level: 'INFO', message: `: ${fileToRemove.name}`, timestamp: new Date().toISOString() });
    }

    setFiles(prevFiles => prevFiles.filter(f => f.id !== fileId));

    if (fileId === primaryFileId) {
      setPrimaryFileId(null);
      setInvoiceNumber('');
      setClientOrderNumber('');
      setTotalAmount('');
    }
  };

  const handleAutomation = async () => {
    if (!clientOrderNumber) {
      message.error('Client Order Number ');
      return;
    }

    if (!primaryFileId) {
      message.error('');
      return;
    }

    // Generate unique sessionId for this automation task
    const sessionId = `session-${Date.now()}-${Math.random().toString(36).substr(2, 9)}`;
    let eventSource = null;

    setAutomating(true);
    setProcessingProgress(0);

    try {
      // Connect to session-specific log stream
      eventSource = new EventSource(`http://localhost:3001/api/automation-logs?sessionId=${sessionId}`);

      eventSource.onopen = () => {
        console.log(`SSE connection opened for session ${sessionId}`);
      };

      eventSource.onmessage = (event) => {
        const data = JSON.parse(event.data);
        if (data.type === 'log') {
          addLog(data);
        }
      };

      eventSource.onerror = (error) => {
        console.error('Log stream error:', error);
      };

      addLog({ level: 'INFO', message: '===  Procore  ===', timestamp: new Date().toISOString() });
      addLog({ level: 'INFO', message: `: ${invoiceNumber}`, timestamp: new Date().toISOString() });
      addLog({ level: 'INFO', message: `: ${clientOrderNumber}`, timestamp: new Date().toISOString() });
      addLog({ level: 'INFO', message: `: ${totalAmount}`, timestamp: new Date().toISOString() });
      addLog({ level: 'INFO', message: `: ${files.length}`, timestamp: new Date().toISOString() });

      // fileId
      const unuploadedFiles = files.filter(f => !f.fileId);

      if (unuploadedFiles.length > 0) {
        addLog({ level: 'INFO', message: ` ${unuploadedFiles.length} ...`, timestamp: new Date().toISOString() });

        // AIuseAI=false
        for (const file of unuploadedFiles) {
          addLog({ level: 'INFO', message: `: ${file.name}`, timestamp: new Date().toISOString() });

          const formData = new FormData();
          formData.append('pdf', file.file);

          try {
            const uploadResponse = await fetch('/api/upload-pdf?useAI=false', {
              method: 'POST',
              body: formData,
            });

            const result = await uploadResponse.json();

            if (result.success) {
              // fileId
              setFiles(prevFiles => prevFiles.map(f => {
                if (f.id === file.id) {
                  return {
                    ...f,
                    fileId: result.fileInfo.id,
                    uploaded: true
                  };
                }
                return f;
              }));

              addLog({ level: 'INFO', message: ` ${file.name} `, timestamp: new Date().toISOString() });
            } else {
              addLog({ level: 'ERROR', message: ` ${file.name} `, timestamp: new Date().toISOString() });
            }
          } catch (uploadError) {
            addLog({ level: 'ERROR', message: ` ${file.name} : ${uploadError.message}`, timestamp: new Date().toISOString() });
          }
        }

        // 
        await new Promise(resolve => setTimeout(resolve, 500));
      }

      // fileId
      const currentFiles = await new Promise(resolve => {
        setFiles(prevFiles => {
          resolve(prevFiles);
          return prevFiles;
        });
      });

      const fileIds = currentFiles.map(f => f.fileId).filter(id => id);

      addLog({ level: 'INFO', message: `Procore (${fileIds.length}):`, timestamp: new Date().toISOString() });
      currentFiles.forEach((f, index) => {
        addLog({
          level: 'INFO',
          message: `  ${index + 1}. ${f.name} -> fileId: ${f.fileId || 'undefined'}`,
          timestamp: new Date().toISOString()
        });
      });

      const response = await fetch('/api/automate', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          clientOrderNumber,
          invoiceNumber,
          totalAmount,
          fileId: primaryFileId,
          fileIds: fileIds,
        }),
      });

      const result = await response.json();

      if (result.success) {
        setProcessingProgress(100);
        message.success('');
        addLog({ level: 'INFO', message: '===   ===', timestamp: new Date().toISOString() });

        // 
        showCompletionNotification();

        setTimeout(() => {
          setProcessingProgress(0);
        }, 3000);
      } else {
        message.error(': ' + (result.error || ''));
      }
    } catch (error) {
      message.error(': ' + error.message);
    } finally {
      setAutomating(false);

      // Close session-specific event source
      if (eventSource) {
        console.log(`Closing SSE connection for session ${sessionId}`);
        eventSource.close();
      }
    }
  };

  // Load settings
  const loadSettings = async () => {
    try {
      const response = await fetch('http://localhost:3001/api/settings');
      const result = await response.json();
      if (result.success) {
        settingsForm.setFieldsValue({
          openaiApiKey: result.settings.openaiApiKey || '',
          procoreEmail: result.settings.procoreEmail || '',
          procorePassword: result.settings.procorePassword || ''
        });
        setPoMappings(result.settings.poMappings || []);
      }
    } catch (error) {
      console.error('Failed to load settings:', error);
    }
  };

  // Save settings
  const handleSaveSettings = async (values) => {
    setSavingSettings(true);
    try {
      const response = await fetch('http://localhost:3001/api/settings', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          openaiApiKey: values.openaiApiKey,
          procoreEmail: values.procoreEmail,
          procorePassword: values.procorePassword,
          poMappings: poMappings
        }),
      });

      const result = await response.json();

      if (result.success) {
        message.success('');
        setSettingsVisible(false);
      } else {
        message.error(': ' + (result.error || ''));
      }
    } catch (error) {
      message.error(': ' + error.message);
    } finally {
      setSavingSettings(false);
    }
  };

  // Add PO mapping
  const handleAddPoMapping = () => {
    setPoMappings([...poMappings, { poPattern: '', projectName: '' }]);
  };

  // Remove PO mapping
  const handleRemovePoMapping = (index) => {
    const newMappings = poMappings.filter((_, i) => i !== index);
    setPoMappings(newMappings);
  };

  // Update PO mapping
  const handleUpdatePoMapping = (index, field, value) => {
    const newMappings = [...poMappings];
    newMappings[index][field] = value;
    setPoMappings(newMappings);
  };

  // Test API Key
  const handleTestApi = async () => {
    try {
      console.log('Testing API Key...');
      const values = await settingsForm.validateFields(['openaiApiKey']);
      console.log('Form validated, API Key:', values.openaiApiKey);
      setTestingApi(true);

      const response = await fetch('http://localhost:3001/api/settings/test', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          openaiApiKey: values.openaiApiKey
        }),
      });

      console.log('Response status:', response.status);
      const result = await response.json();
      console.log('Response data:', result);

      if (result.success) {
        message.success(result.message || 'API Key ');
      } else {
        message.error(result.error || 'API Key ');
      }
    } catch (error) {
      console.error('API test error:', error);
      if (error.errorFields) {
        message.warning(' API Key');
      } else {
        message.error(': ' + error.message);
      }
    } finally {
      setTestingApi(false);
    }
  };

  // Open settings dialog
  const handleOpenSettings = () => {
    loadSettings();
    setSettingsVisible(true);
  };

  const uploadProps = {
    name: 'file',
    multiple: true,
    accept: '.pdf,.jpg,.jpeg,.png',
    showUploadList: false,
    beforeUpload: (file, fileList) => {
      handleFiles(fileList);
      return false;
    },
  };

  const getLogColor = (level) => {
    switch (level) {
      case 'ERROR': return 'error';
      case 'WARN': return 'warning';
      case 'INFO': return 'processing';
      default: return 'default';
    }
  };

  return (
    <Layout style={{ minHeight: '100vh', height: '100vh', display: 'flex', flexDirection: 'column' }}>
      {/* Header */}
      <Header style={{
        background: '#fff',
        padding: '16px 50px',
        boxShadow: '0 2px 8px rgba(0,0,0,0.1)',
        display: 'flex',
        alignItems: 'center',
        height: 'auto',
        lineHeight: 'normal',
        flex: '0 0 auto',
        justifyContent: 'space-between'
      }}>
        <div style={{ display: 'flex', alignItems: 'center' }}>
          <FileTextOutlined style={{ fontSize: 32, color: '#1890ff', marginRight: 16 }} />
          <div style={{ display: 'flex', flexDirection: 'column', gap: 4 }}>
            <div style={{ display: 'flex', alignItems: 'baseline', gap: 12 }}>
              <Title level={3} style={{ margin: 0, lineHeight: 1.2 }}>Invoice PDF </Title>
              <Text type="secondary" style={{ fontSize: 11 }}>v{packageJson.version}</Text>
            </div>
            <Text type="secondary" style={{ fontSize: 12, lineHeight: 1.5 }}> PDF  Procore</Text>
          </div>
        </div>
        <Button
          icon={<SettingOutlined />}
          onClick={handleOpenSettings}
          type="text"
        >
          
        </Button>
      </Header>

      <Layout style={{ flex: 1, overflow: 'hidden' }}>
        {/* Left Sidebar - Data & Controls */}
        <Sider width={450} theme="light" style={{
          background: '#f5f5f5',
          padding: '24px',
          overflow: 'auto',
          height: '100%'
        }}>
          {/* Extracted Data Card */}
          <Card
            title={
              <Space>
                <FileAddOutlined />
                
                {uploading && <LoadingOutlined spin />}
              </Space>
            }
            style={{ marginBottom: 16 }}
            size="small"
          >
            {/* File List */}
            {files.length > 0 && (
              <>
                <Paragraph style={{ marginBottom: 8, fontWeight: 500 }}>
                    ({files.length})
                </Paragraph>
                <Radio.Group
                  value={primaryFileId}
                  onChange={(e) => handleSelectPrimaryFile(e.target.value)}
                  style={{ width: '100%' }}
                >
                  <Space direction="vertical" style={{ width: '100%' }}>
                    {files.map((file) => (
                      <Card
                        key={file.id}
                        size="small"
                        bodyStyle={{ padding: '8px 12px' }}
                        style={{
                          background: primaryFileId === file.id ? '#e6f7ff' : '#fff',
                          cursor: 'pointer'
                        }}
                        onClick={() => handleSelectPrimaryFile(file.id)}
                      >
                        <Row gutter={8} align="middle">
                          <Col flex="auto">
                            <Radio value={file.id} style={{ marginRight: 8 }} />
                            <Text
                              ellipsis
                              style={{
                                fontFamily: 'monospace',
                                fontSize: 12
                              }}
                            >
                              {file.name}
                            </Text>
                          </Col>
                          <Col>
                            <Space size="small">
                              <Button
                                size="small"
                                type="primary"
                                ghost
                                icon={recognizingFileId === file.id ? <LoadingOutlined /> : <EyeOutlined />}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  handleRecognizeFile(file.id);
                                }}
                                loading={recognizingFileId === file.id}
                              >
                                
                              </Button>
                              <Button
                                size="small"
                                danger
                                icon={<DeleteOutlined />}
                                onClick={(e) => {
                                  e.stopPropagation();
                                  handleRemoveFile(file.id);
                                }}
                              />
                            </Space>
                          </Col>
                        </Row>
                      </Card>
                    ))}
                  </Space>
                </Radio.Group>
                <Text type="secondary" style={{ fontSize: 11, display: 'block', marginTop: 8 }}>
                   ""AI
                </Text>
                <Divider style={{ margin: '12px 0' }} />
              </>
            )}

            {/* Extracted Fields */}
            {invoiceNumber || clientOrderNumber || totalAmount ? (
              <Space direction="vertical" style={{ width: '100%' }}>
                <div>
                  <Text type="secondary" style={{ fontSize: 12 }}></Text>
                  <Input
                    value={invoiceNumber}
                    onChange={(e) => setInvoiceNumber(e.target.value)}
                    placeholder=": 335397"
                    style={{ marginTop: 4 }}
                  />
                </div>
                <div>
                  <Text type="secondary" style={{ fontSize: 12 }}>/PO</Text>
                  <Input
                    value={clientOrderNumber}
                    onChange={(e) => setClientOrderNumber(e.target.value)}
                    placeholder=": KIWIWASTE-006"
                    style={{ marginTop: 4 }}
                  />
                </div>
                <div>
                  <Text type="secondary" style={{ fontSize: 12 }}></Text>
                  <Input
                    value={totalAmount}
                    onChange={(e) => setTotalAmount(e.target.value)}
                    placeholder=": 1137.81"
                    style={{ marginTop: 4 }}
                  />
                </div>

                {processingProgress > 0 && (
                  <Progress percent={processingProgress} status="active" />
                )}

                <Button
                  type="primary"
                  size="large"
                  icon={automating ? <LoadingOutlined /> : <PlayCircleOutlined />}
                  onClick={handleAutomation}
                  disabled={automating || !clientOrderNumber}
                  loading={automating}
                  block
                >
                  {automating ? '...' : ''}
                </Button>
              </Space>
            ) : files.length === 0 ? (
              <Text type="secondary" style={{ display: 'block', textAlign: 'center', padding: '20px 0' }}>
                {uploading ? ' ...' : ' PDF ...'}
              </Text>
            ) : (
              <Text type="secondary" style={{ display: 'block', textAlign: 'center', padding: '20px 0' }}>
                ""
              </Text>
            )}
          </Card>

          {/* Logs Card */}
          <Card
            title={<Space><FileTextOutlined /></Space>}
            size="small"
            bodyStyle={{ padding: 0 }}
          >
            <div
              ref={logConsoleRef}
              style={{
                height: 300,
                overflow: 'auto',
                background: '#000',
                padding: '8px',
                fontFamily: 'monospace',
                fontSize: 11
              }}
            >
              {logs.length === 0 ? (
                <Text style={{ color: '#52c41a' }}>
                  [{new Date().toLocaleTimeString('zh-CN')}] [INFO] ...
                </Text>
              ) : (
                logs.map((log, index) => (
                  <div key={index} style={{
                    color: log.level === 'ERROR' ? '#ff4d4f' :
                           log.level === 'WARN' ? '#faad14' : '#52c41a',
                    marginBottom: 2
                  }}>
                    [{new Date(log.timestamp).toLocaleTimeString('zh-CN')}] [{log.level}] {log.message}
                  </div>
                ))
              )}
            </div>
          </Card>
        </Sider>

        {/* Main Content - File Preview */}
        <Content style={{ padding: 24, background: '#fff', height: '100%', overflow: 'hidden', display: 'flex', flexDirection: 'column' }}>
          {primaryFileId && files.find(f => f.id === primaryFileId) ? (
            <div style={{ flex: 1, border: '1px solid #d9d9d9', borderRadius: 4, overflow: 'hidden', display: 'flex', alignItems: 'center', justifyContent: 'center', background: '#f0f0f0' }}>
              {(() => {
                const selectedFile = files.find(f => f.id === primaryFileId);
                const isImage = selectedFile.file?.type?.startsWith('image/') ||
                               selectedFile.name?.match(/\.(jpg|jpeg|png)$/i);

                if (isImage) {
                  return (
                    <img
                      src={selectedFile.url}
                      alt={selectedFile.name}
                      style={{ maxWidth: '100%', maxHeight: '100%', objectFit: 'contain' }}
                    />
                  );
                } else {
                  return (
                    <iframe
                      src={selectedFile.url}
                      title="PDF Preview"
                      style={{ width: '100%', height: '100%', border: 'none' }}
                    />
                  );
                }
              })()}
            </div>
          ) : (
            <Dragger
              {...uploadProps}
              style={{
                flex: 1,
                display: 'flex',
                alignItems: 'center',
                justifyContent: 'center'
              }}
            >
              <p className="ant-upload-drag-icon">
                <InboxOutlined style={{ fontSize: 80, color: '#1890ff' }} />
              </p>
              <p className="ant-upload-text" style={{ fontSize: 18, fontWeight: 500 }}>
                
              </p>
              <p className="ant-upload-hint" style={{ fontSize: 14 }}>
                 PDFJPGPNG 
              </p>
            </Dragger>
          )}
        </Content>
      </Layout>

      {/* Settings Modal */}
      <Modal
        title=""
        open={settingsVisible}
        onCancel={() => setSettingsVisible(false)}
        footer={null}
        width={700}
      >
        <Form
          form={settingsForm}
          onFinish={handleSaveSettings}
          layout="vertical"
        >
          <Form.Item
            label="OpenAI API Key"
            name="openaiApiKey"
            rules={[
              { required: true, message: ' OpenAI API Key' },
              { pattern: /^sk-/, message: 'API Key  sk- ' }
            ]}
            extra=" OpenAI API Key GPT-4 Vision "
          >
            <Input.Password
              placeholder="sk-proj-..."
              style={{ fontFamily: 'monospace' }}
            />
          </Form.Item>

          <Divider orientation="left">Procore </Divider>

          <Form.Item
            label="Procore Email"
            name="procoreEmail"
            extra="Procore"
          >
            <Input
              placeholder="your-email@example.com"
              type="email"
            />
          </Form.Item>

          <Form.Item
            label="Procore Password"
            name="procorePassword"
            extra="Procore"
          >
            <Input.Password
              placeholder=""
            />
          </Form.Item>

          <Divider orientation="left">PO</Divider>

          <Form.Item
            label={
              <span>
                PO
                <Text type="secondary" style={{ fontSize: 12, marginLeft: 8 }}>
                  (PO)
                </Text>
              </span>
            }
          >
            {poMappings.map((mapping, index) => (
              <Row gutter={8} key={index} style={{ marginBottom: 8 }}>
                <Col span={10}>
                  <Input
                    placeholder="PO (: MEADOWOOD)"
                    value={mapping.poPattern}
                    onChange={(e) => handleUpdatePoMapping(index, 'poPattern', e.target.value)}
                  />
                </Col>
                <Col span={12}>
                  <Input
                    placeholder=" (: MEADOWOOD - Meadowood 27 Civil Works)"
                    value={mapping.projectName}
                    onChange={(e) => handleUpdatePoMapping(index, 'projectName', e.target.value)}
                  />
                </Col>
                <Col span={2}>
                  <Button
                    danger
                    icon={<DeleteOutlined />}
                    onClick={() => handleRemovePoMapping(index)}
                  />
                </Col>
              </Row>
            ))}
            <Button
              type="dashed"
              onClick={handleAddPoMapping}
              block
              style={{ marginTop: 8 }}
            >
              + 
            </Button>
            <Text type="secondary" style={{ fontSize: 11, display: 'block', marginTop: 8 }}>
               PO "MEADOWOOD-02"  "MEADOWOOD"
            </Text>
          </Form.Item>

          <Form.Item>
            <Space>
              <Button type="primary" htmlType="submit" loading={savingSettings}>
                
              </Button>
              <Button onClick={handleTestApi} loading={testingApi} icon={<ApiOutlined />}>
                 API
              </Button>
              <Button onClick={() => setSettingsVisible(false)}>
                
              </Button>
            </Space>
          </Form.Item>
        </Form>
      </Modal>
    </Layout>
  );
}

export default App;
